<template>
    <div class="wrapper" ref="wrapper">
        <div class="scroll_content" ref="scrollContent">
            <slot :pulling="pulling" :beforePullDown="beforePullDown">
             
            </slot>
            <div class="pullup-wrapper" v-if="pullUpLoad" v-show="pullUpShow">
                <div class="before-trigger" v-if="!isPullUpLoad">
                    <span>pullUp-loadMore</span>
                </div>
                <div class="after-trigger" v-else>
                    <loading></loading>
                </div>
            </div>
            
        </div>
        <div ref="pulldown" class="pulldown-wrapper" :style="pullDownStyle">
            <div class="before-trigger" v-if="beforePullDown">
                <bubble :y="bubbleY"></bubble>
            </div>
            <div class="after-trigger">
                <div v-show="pulling" class="loading" >
                    <loading></loading>
                </div>
                <div><span></span></div>
            </div>
        </div>    
    </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'
  import bubble from "./../base/bubble.vue"
  import loading from "./../base/loading.vue"

  export default {
    props: {
      /**
       * 1 scroll
       * 2 scroll
       * 3 scrollswipescroll
       */
      loadDatas: {
        type: Array,
        default: function () {
          return []
        }
      },
      probeType: {
        type: Number,
        default: 1
      },
      /**
       * click
       */
      click: {
        type: Boolean,
        default: true
      },
      /**
       * 
       */
      scrollX: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      listenScroll: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      pullUpLoad: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      pulldown: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      beforeScroll: {
        type: Boolean,
        default: false
      },
      /**
       * scroll
       */
      refreshDelay: {
        type: Number,
        default: 20
      },
    },
    data() {
        return {
            bubbleY: 0,
            pulling: false,
            beforePullDown: true,
            pullDownStyle: "",
            isRebounding: false,
            isPullUpLoad: false,
            pullUpShow: true,
        }
    },
    components: {
        bubble,
        loading
    },
    created() {
        this.pullDownInitTop = -50;
        setTimeout(() => {
            this.initScroll();
            this.judgeHeight();
        }, 20);
    },
    methods: {
        refresh() {
            this.scroll && this.scroll.refresh();
        },
        initScroll() {
            this.scroll = new BScroll(this.$refs.wrapper, {//better-scroll
                probeType: this.probeType,
                click: this.click,
                scrollX: this.scrollX,
                pullDownRefresh: this.pulldown,//
                pullUpLoad: this.pullUpLoad,//
            });

            if (this.pulldown) {
                this._pullDownRefresh();
            };

            if (this.pullUpLoad) {
                this._pullUpLoad();
            }
        },
        _pullDownRefresh() {//
            this.scroll.on("pullingDown", () => {
                
                this.beforePullDown = false;
                this.pulling = true;
                this.$emit("pullingDown");//pullingDown
            });

            this.scroll.on('scroll', (pos) => {//
                if (this.beforePullDown) {
                    this.bubbleY = Math.max(0, pos.y + this.pullDownInitTop)
                    this.pullDownStyle = `top:${Math.min(pos.y + this.pullDownInitTop, 10)}px`
                } else {
                    this.bubbleY = 0
                }
                if (this.isRebounding) {
                    this.pullDownStyle = `top:${this.pullDownInitTop}px`
                }
            });
 
        },
        _pullUpLoad() {//
            
            this.scroll.on("pullingUp", () => {
                this.isPullUpLoad = true;
                this.$emit("pullingUp");//pullingUp
            });
        },
        _afterPullDown() {//
            setTimeout(() => {
                this.pullDownStyle = `top:${this.pullDownInitTop}px`;
                this.beforePullDown = true;
                this.pulling = false;
                this.isRebounding = false;
                this.refresh();//better-scroll
            }, this.scroll.options.bounceTime)
        },
        forceUpdate() {//
            this.isPullUpLoad = false;
            this.isRebounding = true;
            
            this.scroll.finishPullDown();//  better-scroll 
            this.scroll.finishPullUp();//   better-scroll 
            this._afterPullDown();

        },
        judgeHeight() {// 
            let scrollChildrenHeight = this.$refs.scrollContent.clientHeight,
                wrapperHenght = this.$refs.wrapper.clientHeight;

            if (scrollChildrenHeight >= wrapperHenght) {
                this.pullUpShow = true;
            } else {
                this.pullUpShow = false;
            }
        },

    },
    watch: {
      loadDatas() {//, 
        setTimeout(() => {
          this.forceUpdate();
        }, 20)
      }
    },
  }
</script>

<style lang="scss" scoped>
    @import "./../common/sass/var.scss";

    .wrapper {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
    }
    .pulldown-wrapper {
        position: absolute;
        width: 100%;
        left: 0;
        top: -50px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all;
        .after-trigger {
            //  margin-top: 10px;
        } 
    }
    .pullup-wrapper {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 16px 0;
    }
    
</style>



